import MyFooter from "../../components/footer.vue"
import Detail from "../../components/detail.vue"
import Statistics from "../../components/statistics.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import addDetail from "@/components/add-detail.vue"
export default {
	data() {
		return {
			title: 'Hello',
			nowIndex: 0,
			types: []
		}
	},
	onLoad() {
		this.getTypes();
		this.$util.Bus.$on('add-finish', this.$refs.addDetail.close)
		this.$util.Bus.$on('add-type', this.getTypes)
	},
	methods: {
		async getTypes() {
			let res = await this.$myRequest('/getType', {}, 'GET');
			if (res) {
				this.types = res;
			}
		},
		switchPage(index) {
			if (index == 1) {
				this.$refs.addDetail.open()
			} else {
				this.nowIndex = index;
			}
		}
	},
	components: {
		MyFooter,
		Detail,
		Statistics,
		uniPopup,
		addDetail
	}
}
